import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { ILocalizedStrings } from './localization.types';

@Injectable({
  providedIn: 'root'
})
export class RxLocalizedStringsLoaderService {
  baseUrl = '/api/rx/application/localizedstrings';

  constructor(private httpClient: HttpClient) {}

  getTranslation(locale: string): Observable<ILocalizedStrings> {
    return locale === 'login' ? of({}) : this.httpClient.get<ILocalizedStrings>(`${this.baseUrl}?locale=${locale}`);
  }

  uploadTranslation(bundleId: string, locale: string, translations: Object): Observable<any> {
    return this.httpClient.post(`${this.baseUrl}/${bundleId}?locale=${locale}`, translations);
  }
}
